$primary: #1572e8;
$secondary: #f25961;
$border: #434a52;

$grey: #b9b9b9;
$grey0: #454545;
$grey2: #d8dee5;
$grey3: #ebeef2;
$blue: #0167c1;
$black: #000;
$dark: #1a2035;
$white: #fff;
$red: #ff0000;
$red5: #de1c24;
$green: #1cde93;
$yellow: #f8a700;

$small: 14px;
$normal: 20px;

.text__field {
  background: transparent;
  border: none;
  border: 1px solid #434a52;
  color: $black;
  width: 100%;
  display: block;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 10px;
  border-radius: 5px;
}

.text__container {
  margin-top: 30px;
}

.error {
  position: absolute;
  color: $secondary;
}
