$primary: #1572e8;
$secondary: #f25961;
$border: #434a52;

$grey: #b9b9b9;
$grey0: #454545;
$grey2: #d8dee5;
$grey3: #ebeef2;
$blue: #0167c1;
$black: #000;
$dark: #1a2035;
$white: #fff;
$red: #ff0000;
$red5: #de1c24;
$green: #1cde93;
$yellow: #f8a700;

$small: 14px;
$normal: 20px;

.container {
  padding-top: 50px;
  padding-bottom: 50px;
  justify-content: space-between;
  display: flex;
  height: 900px;
  width: 80%;

  background-color: $white;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.2);
  flex-direction: column;
}

// .input__fields {
//   padding-right: 500px !important;
// }

.input__container {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
}
.displayRow {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.groupBtn {
  border: none;
  font-size: 20px;
  margin-top: 20px;
  margin-right: 20px;
  width: 250px;
}

.group {
  display: flex;
  justify-content: flex-end;
}
