$primary: #1572e8;
$secondary: #f25961;
$border: #434a52;

$grey: #b9b9b9;
$grey0: #454545;
$grey2: #d8dee5;
$grey3: #ebeef2;
$blue: #0167c1;
$black: #000;
$dark: #1a2035;
$white: #fff;
$red: #ff0000;
$red5: #de1c24;
$green: #1cde93;
$yellow: #f8a700;

$small: 14px;
$normal: 20px;

.hara__dropdown {
  width: 100%;

  & + div {
    width: 100%;
  }
  .hara__select {
    &__menu {
      z-index: 1000;
    }

    &__indicator-separator {
      width: 0;
    }

    &__single-value {
      font-size: 1rem;
      color: #000;
    }

    &__control {
      border-radius: 4px;
      background-color: $white;
      font-size: 1rem;
      line-height: 24px;

      &--is-focused {
        border: 1px solid $blue;
        box-shadow: none;
        &:hover {
          border-color: $blue !important;
        }
      }
      &--is-disabled {
        border: 1px solid $grey3;
      }

      &:hover {
        border-color: hsl(0, 0%, 80%);
      }
    }

    &__value-container {
      padding-left: 16.5px;

      & > div {
        margin: 0;
      }
    }

    &__placeholder,
    &__single-value {
      margin: 0;
      left: 16.5px;
    }

    &__Input {
      margin: 0;
    }
  }
}
