$primary: #1572e8;
$secondary: #f25961;
$border: #434a52;

$grey: #b9b9b9;
$grey0: #454545;
$grey2: #d8dee5;
$grey3: #ebeef2;
$blue: #0167c1;
$black: #000;
$dark: #1a2035;
$white: #fff;
$red: #ff0000;
$red5: #de1c24;
$green: #1cde93;
$yellow: #f8a700;

$small: 14px;
$normal: 20px;

.md__modal {
  display: flex !important;
  align-items: center;
  justify-content: center;

  .md__modal__dialog {
    box-shadow: 0 16px 40px 0 rgba($black, 0.5);

    .modal-content {
      max-height: 90vh;
      padding: 36px 0 22px 0;
      position: relative;

      .md__modal__content-wrapper {
        height: 100%;
        overflow: auto;
        padding: 0 40px;

        & > .flaticon-cross {
          position: absolute;
          right: 40px;
          font-size: 16px;
          cursor: pointer;

          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }

  @media (min-width: 576px) {
    .modal-dialog {
      width: 460px;
    }
  }
}
